
import React, { useState, useEffect, useRef } from 'react';
import { X, Sparkles, Loader2, Save } from 'lucide-react';
import { Transaction, TransactionType } from '../types';
import { parseSmartEntry } from '../geminiService';

interface TransactionFormProps {
  onClose: () => void;
  onSubmit: (transaction: Omit<Transaction, 'id' | 'createdBy'>) => void;
  initialData?: Transaction;
}

const INCOME_OPTIONS = [
  'Iuran Bulanan',
  'Bunga Deposito',
  'Bunga Tabungan',
  'Lain-lain'
];

const TransactionForm: React.FC<TransactionFormProps> = ({ onClose, onSubmit, initialData }) => {
  const [smartInput, setSmartInput] = useState('');
  const [isParsing, setIsParsing] = useState(false);

  // Default ke 'income' (Pemasukan) jika transaksi baru
  const [type, setType] = useState<TransactionType>(initialData?.type || 'income');
  const [description, setDescription] = useState(initialData?.description || '');
  const [amount, setAmount] = useState(initialData?.amount?.toString() || '');
  const [category, setCategory] = useState(initialData?.category || 'Umum');
  const [date, setDate] = useState(initialData?.date || new Date().toISOString().split('T')[0]);
  
  // State untuk dropdown pemasukan
  const [incomeSelect, setIncomeSelect] = useState(() => {
    if (initialData?.type === 'income') {
      return INCOME_OPTIONS.includes(initialData.description) ? initialData.description : 'Lain-lain';
    }
    return INCOME_OPTIONS[0];
  });

  const descriptionInputRef = useRef<HTMLInputElement>(null);
  const selectRef = useRef<HTMLSelectElement>(null);

  const isEditing = !!initialData;

  // Handler pindah ke Pemasukan
  const switchToIncome = () => {
    setType('income');
    if (incomeSelect !== 'Lain-lain') {
      setDescription(incomeSelect);
    } else {
      setDescription('');
    }
    // Memberi sedikit delay agar DOM terupdate sebelum fokus
    setTimeout(() => {
      if (incomeSelect === 'Lain-lain') {
        descriptionInputRef.current?.focus();
      } else {
        selectRef.current?.focus();
      }
    }, 50);
  };

  // Handler pindah ke Pengeluaran
  const switchToExpense = () => {
    setType('expense');
    setDescription(''); // Sesuai permintaan: kosongkan saat klik pengeluaran
    setTimeout(() => {
      descriptionInputRef.current?.focus();
    }, 50);
  };

  // Efek untuk menyinkronkan deskripsi saat dropdown berubah (Hanya jika mode income)
  useEffect(() => {
    if (type === 'income') {
      if (incomeSelect !== 'Lain-lain') {
        setDescription(incomeSelect);
      } else if (!isEditing) {
        // Jika pilih lain-lain di mode baru, kosongkan dan fokus
        setDescription('');
        descriptionInputRef.current?.focus();
      }
    }
  }, [incomeSelect, type]);

  // Fokus otomatis saat modal pertama kali terbuka
  useEffect(() => {
    if (!isEditing) {
      if (type === 'income') {
        selectRef.current?.focus();
      } else {
        descriptionInputRef.current?.focus();
      }
    }
  }, []);

  const handleSmartParse = async () => {
    if (!smartInput.trim()) return;
    setIsParsing(true);
    const parsed = await parseSmartEntry(smartInput);
    if (parsed) {
      setDescription(parsed.description || '');
      setAmount(parsed.amount?.toString() || '');
      setCategory(parsed.category || 'Umum');
      setType(parsed.type || 'expense');
      setSmartInput('');
    }
    setIsParsing(false);
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!description || !amount) return;
    onSubmit({
      description,
      amount: parseFloat(amount),
      category: category || 'Umum',
      type,
      date
    });
  };

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-slate-900/60 backdrop-blur-sm">
      <div className="bg-white w-full max-w-lg rounded-3xl shadow-2xl overflow-hidden animate-in fade-in zoom-in duration-300">
        <div className="flex items-center justify-between p-6 border-b">
          <h3 className="text-xl font-bold text-slate-900">
            {isEditing ? 'Edit Transaksi' : 'Tambah Transaksi'}
          </h3>
          <button onClick={onClose} className="p-2 text-slate-400 hover:text-slate-600 rounded-lg">
            <X size={24} />
          </button>
        </div>

        <div className="p-6">
          {!isEditing && (
            <div className="mb-8 p-4 bg-blue-50 rounded-2xl border border-blue-100">
              <label className="text-xs font-bold text-blue-600 uppercase mb-2 block flex items-center">
                <Sparkles size={14} className="mr-1" /> Input Pintar (AI)
              </label>
              <div className="flex gap-2">
                <input 
                  type="text" 
                  placeholder="Contoh: 'Terima bunga tabungan 50rb'" 
                  className="flex-1 bg-white border-blue-200 rounded-xl px-4 py-2 text-sm focus:ring-2 focus:ring-blue-500 focus:outline-none"
                  value={smartInput}
                  onChange={(e) => setSmartInput(e.target.value)}
                  onKeyDown={(e) => e.key === 'Enter' && handleSmartParse()}
                />
                <button 
                  type="button"
                  onClick={handleSmartParse}
                  disabled={isParsing || !smartInput.trim()}
                  className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-xl text-sm font-medium disabled:opacity-50 transition-all flex items-center"
                >
                  {isParsing ? <Loader2 size={16} className="animate-spin" /> : 'Proses'}
                </button>
              </div>
            </div>
          )}

          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="grid grid-cols-2 gap-4">
              <button
                type="button"
                onClick={switchToIncome}
                className={`py-3 px-4 rounded-xl border-2 transition-all font-semibold ${
                  type === 'income' 
                  ? 'border-green-500 bg-green-50 text-green-700' 
                  : 'border-slate-100 text-slate-400'
                }`}
              >
                Pemasukan
              </button>
              <button
                type="button"
                onClick={switchToExpense}
                className={`py-3 px-4 rounded-xl border-2 transition-all font-semibold ${
                  type === 'expense' 
                  ? 'border-red-500 bg-red-50 text-red-700' 
                  : 'border-slate-100 text-slate-400'
                }`}
              >
                Pengeluaran
              </button>
            </div>

            <div className="space-y-3">
              <label className="text-sm font-semibold text-slate-700 block">Deskripsi / Keterangan</label>
              
              {type === 'income' ? (
                <div className="space-y-3">
                  <select
                    ref={selectRef}
                    className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:outline-none transition-all"
                    value={incomeSelect}
                    onChange={(e) => setIncomeSelect(e.target.value)}
                  >
                    {INCOME_OPTIONS.map(opt => (
                      <option key={opt} value={opt}>{opt}</option>
                    ))}
                  </select>
                  
                  {incomeSelect === 'Lain-lain' && (
                    <input 
                      required
                      ref={descriptionInputRef}
                      type="text" 
                      placeholder="Masukkan keterangan manual..."
                      className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:outline-none transition-all animate-in slide-in-from-top-1 duration-200"
                      value={description}
                      onChange={(e) => setDescription(e.target.value)}
                    />
                  )}
                </div>
              ) : (
                <input 
                  required
                  ref={descriptionInputRef}
                  type="text" 
                  placeholder="Apa keperluan pengeluaran ini?"
                  className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:outline-none transition-all"
                  value={description}
                  onChange={(e) => setDescription(e.target.value)}
                />
              )}
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="text-sm font-semibold text-slate-700 mb-1 block">Jumlah (IDR)</label>
                <input 
                  required
                  type="number" 
                  inputMode="decimal"
                  placeholder="0"
                  className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:outline-none transition-all font-bold text-slate-900 text-lg"
                  value={amount}
                  onChange={(e) => setAmount(e.target.value)}
                />
              </div>
              <div>
                <label className="text-sm font-semibold text-slate-700 mb-1 block">Tanggal</label>
                <input 
                  type="date" 
                  className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:outline-none transition-all"
                  value={date}
                  onChange={(e) => setDate(e.target.value)}
                />
              </div>
            </div>

            <button 
              type="submit"
              className="w-full bg-slate-900 hover:bg-slate-800 text-white py-4 rounded-2xl font-bold shadow-xl transition-all flex items-center justify-center space-x-2 mt-6"
            >
              <Save size={20} />
              <span>{isEditing ? 'Perbarui Transaksi' : 'Simpan Transaksi'}</span>
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default TransactionForm;
