
import React from 'react';
import { History, User, Clock, Info } from 'lucide-react';
import { ActivityLog } from '../types';

interface ActivityLogListProps {
  logs: ActivityLog[];
}

const ActivityLogList: React.FC<ActivityLogListProps> = ({ logs }) => {
  const getActionStyle = (action: ActivityLog['action']) => {
    switch (action) {
      case 'CREATE': return 'text-green-600 bg-green-50 border-green-100';
      case 'UPDATE': return 'text-blue-600 bg-blue-50 border-blue-100';
      case 'DELETE': return 'text-red-600 bg-red-50 border-red-100';
      default: return 'text-slate-600 bg-slate-50 border-slate-100';
    }
  };

  if (logs.length === 0) {
    return (
      <div className="bg-white rounded-3xl p-12 text-center border border-dashed border-slate-200">
        <History className="mx-auto text-slate-200 mb-4" size={48} />
        <p className="text-slate-400">Belum ada log aktivitas yang tercatat.</p>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      <div className="bg-blue-50 p-4 rounded-2xl border border-blue-100 flex items-start space-x-3 text-blue-700">
        <Info size={20} className="shrink-0 mt-0.5" />
        <p className="text-xs leading-relaxed">
          Log ini mencatat setiap aksi krusial yang dilakukan oleh admin. Gunakan log ini untuk melacak perubahan data, terutama transaksi yang telah dihapus oleh admin tertentu.
        </p>
      </div>
      
      <div className="bg-white rounded-3xl shadow-sm border border-slate-100 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full text-left table-auto">
            <thead>
              <tr className="bg-slate-50 border-b border-slate-100">
                <th className="px-6 py-4 text-xs font-bold text-slate-500 uppercase tracking-wider">Waktu</th>
                <th className="px-6 py-4 text-xs font-bold text-slate-500 uppercase tracking-wider">Admin</th>
                <th className="px-6 py-4 text-xs font-bold text-slate-500 uppercase tracking-wider">Aksi</th>
                <th className="px-6 py-4 text-xs font-bold text-slate-500 uppercase tracking-wider">Detail</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-slate-50">
              {logs.map((log) => (
                <tr key={log.id} className="hover:bg-slate-50/30 transition-colors">
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center text-xs text-slate-500">
                      <Clock size={12} className="mr-2" />
                      {new Date(log.timestamp).toLocaleString('id-ID', {
                        day: '2-digit', month: '2-digit', year: '2-digit',
                        hour: '2-digit', minute: '2-digit', second: '2-digit'
                      })}
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center text-sm font-bold text-slate-700">
                      <User size={14} className="mr-2 text-blue-600" />
                      {log.adminName}
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className={`px-3 py-1 rounded-full text-[10px] font-bold border ${getActionStyle(log.action)}`}>
                      {log.action}
                    </span>
                  </td>
                  <td className="px-6 py-4">
                    <div className="text-sm text-slate-600 font-medium line-clamp-1" title={log.details}>
                      {log.details}
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default ActivityLogList;
