
import React, { useState, useEffect } from 'react';
import { Sparkles, TrendingUp, Lightbulb, Target, Loader2, RefreshCw } from 'lucide-react';
import { Transaction, GeminiInsight } from '../types';
import { getFinancialInsights } from '../geminiService';

interface SmartInsightsProps {
  transactions: Transaction[];
}

const SmartInsights: React.FC<SmartInsightsProps> = ({ transactions }) => {
  const [insights, setInsights] = useState<GeminiInsight | null>(null);
  const [loading, setLoading] = useState(false);

  const fetchInsights = async () => {
    if (transactions.length === 0) return;
    setLoading(true);
    const result = await getFinancialInsights(transactions);
    if (result) setInsights(result);
    setLoading(false);
  };

  useEffect(() => {
    fetchInsights();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (transactions.length === 0) {
    return (
      <div className="bg-white rounded-3xl p-12 text-center border border-dashed border-slate-300">
        <div className="mx-auto bg-slate-100 w-16 h-16 rounded-full flex items-center justify-center mb-4">
          <Sparkles className="text-slate-400" size={32} />
        </div>
        <h3 className="text-xl font-bold text-slate-800 mb-2">Belum ada data</h3>
        <p className="text-slate-500">Tambahkan beberapa transaksi untuk mendapatkan analisis cerdas dari AI.</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center mb-2">
        <div className="flex items-center space-x-2 text-blue-600">
          <Sparkles size={20} />
          <h3 className="font-bold text-lg">AI Financial Advisor</h3>
        </div>
        <button 
          onClick={fetchInsights}
          disabled={loading}
          className="p-2 text-slate-400 hover:text-blue-600 hover:bg-blue-50 rounded-xl transition-all"
        >
          {loading ? <Loader2 size={20} className="animate-spin" /> : <RefreshCw size={20} />}
        </button>
      </div>

      {loading ? (
        <div className="bg-white rounded-3xl p-16 flex flex-col items-center justify-center space-y-4 border border-slate-100 shadow-sm">
          <Loader2 className="animate-spin text-blue-600" size={40} />
          <p className="text-slate-500 font-medium">Menganalisis pola transaksi Anda...</p>
        </div>
      ) : insights ? (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 animate-in slide-in-from-bottom-4 duration-500">
          {/* Analysis Card */}
          <div className="bg-white p-8 rounded-3xl border border-slate-100 shadow-sm">
            <div className="flex items-center space-x-3 mb-4 text-blue-600">
              <TrendingUp size={24} />
              <h4 className="font-bold">Analisis Pengeluaran</h4>
            </div>
            <p className="text-slate-600 leading-relaxed italic">
              "{insights.analysis}"
            </p>
          </div>

          {/* Suggestions Card */}
          <div className="bg-white p-8 rounded-3xl border border-slate-100 shadow-sm">
            <div className="flex items-center space-x-3 mb-4 text-orange-500">
              <Lightbulb size={24} />
              <h4 className="font-bold">Saran Perbaikan</h4>
            </div>
            <ul className="space-y-3">
              {insights.suggestions.map((s, i) => (
                <li key={i} className="flex items-start space-x-3 text-slate-600 text-sm">
                  <div className="bg-orange-100 text-orange-600 rounded-full p-1 mt-0.5">
                    <Target size={12} />
                  </div>
                  <span>{s}</span>
                </li>
              ))}
            </ul>
          </div>

          {/* Pro Tip Card - Span 2 */}
          <div className="bg-gradient-to-r from-blue-600 to-indigo-700 p-8 rounded-3xl shadow-xl md:col-span-2 text-white flex flex-col md:flex-row items-center justify-between gap-6 overflow-hidden relative">
            <div className="absolute top-0 right-0 p-10 opacity-10 pointer-events-none">
              <Target size={160} />
            </div>
            <div className="relative z-10 flex-1">
              <div className="flex items-center space-x-2 mb-4">
                <div className="bg-white/20 p-2 rounded-xl">
                  <Sparkles size={20} />
                </div>
                <span className="font-bold tracking-wider uppercase text-xs opacity-90">Expert Saving Tip</span>
              </div>
              <h4 className="text-2xl font-bold mb-4">{insights.savingTip}</h4>
              <p className="text-blue-100 text-sm max-w-2xl">
                Berdasarkan histori transaksi terakhir Anda, mengikuti tips ini dapat membantu menghemat pengeluaran hingga 15% setiap bulan.
              </p>
            </div>
            <div className="relative z-10">
              <button className="bg-white text-blue-600 px-6 py-3 rounded-2xl font-bold shadow-lg shadow-blue-900/20 hover:bg-blue-50 transition-all">
                Terapkan Sekarang
              </button>
            </div>
          </div>
        </div>
      ) : (
        <div className="bg-white rounded-3xl p-16 flex flex-col items-center justify-center space-y-4 border border-slate-100 shadow-sm">
          <p className="text-slate-500">Gagal memuat insight. Silakan coba lagi.</p>
          <button onClick={fetchInsights} className="text-blue-600 font-bold underline">Coba Lagi</button>
        </div>
      )}
    </div>
  );
};

export default SmartInsights;
