
import React, { useEffect, useRef } from 'react';
import { Edit3, PlusCircle, MinusCircle, ShieldAlert, User, Calendar } from 'lucide-react';
import { Transaction } from '../types';

interface TransactionListProps {
  transactions: Transaction[];
  onEdit: (transaction: Transaction) => void;
  isAdmin: boolean;
  isSuperAdmin: boolean;
  loggedAdminName?: string;
  onDelete: (transactionId: string) => void;
}

const TransactionList: React.FC<TransactionListProps> = ({ 
  transactions, 
  onEdit, 
  isAdmin, 
  isSuperAdmin, 
  loggedAdminName,
  onDelete 
}) => {
  const bottomRef = useRef<HTMLDivElement>(null);

  const formatIDR = (amount: number) => {
    return new Intl.NumberFormat('id-ID', {
      style: 'currency',
      currency: 'IDR',
      minimumFractionDigits: 0
    }).format(amount);
  };

  useEffect(() => {
    if (bottomRef.current) {
      bottomRef.current.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }
  }, [transactions]);

  if (transactions.length === 0) {
    return (
      <div className="bg-white rounded-2xl p-12 text-center border border-dashed border-slate-300">
        <p className="text-slate-400 font-medium">Belum ada catatan transaksi.</p>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      <div className="bg-white rounded-2xl border border-slate-100 shadow-sm overflow-hidden flex flex-col">
        <div className="overflow-x-auto">
          <table className="w-full text-left table-auto border-collapse">
            <thead>
              <tr className="bg-slate-50 border-b border-slate-100 text-[11px]">
                <th className="px-6 py-4 font-bold text-slate-500 uppercase tracking-widest">Tanggal</th>
                <th className="px-6 py-4 font-bold text-slate-500 uppercase tracking-widest">Keterangan</th>
                <th className="px-6 py-4 font-bold text-slate-500 uppercase tracking-widest text-right">Debet</th>
                <th className="px-6 py-4 font-bold text-slate-500 uppercase tracking-widest text-right">Kredit</th>
                <th className="px-6 py-4 font-bold text-slate-500 uppercase tracking-widest text-right">Saldo</th>
                {isAdmin && <th className="px-6 py-4 font-bold text-slate-500 uppercase tracking-widest text-center">Aksi</th>}
              </tr>
            </thead>
            <tbody className="divide-y divide-slate-100">
              {transactions.map((t) => {
                const canModify = isSuperAdmin || (loggedAdminName && t.createdBy === loggedAdminName);
                return (
                  <tr key={t.id} className="hover:bg-slate-50 transition-colors group">
                    <td className="px-6 py-4 text-sm text-slate-600 whitespace-nowrap">
                      {new Date(t.date).toLocaleDateString('id-ID', { day: 'numeric', month: 'short', year: 'numeric' })}
                    </td>
                    <td className="px-6 py-4">
                      <div className="flex items-start space-x-3">
                        <div className={`mt-1 p-1.5 rounded-lg shrink-0 ${t.type === 'income' ? 'bg-green-100 text-green-600' : 'bg-red-100 text-red-600'}`}>
                          {t.type === 'income' ? <PlusCircle size={14} /> : <MinusCircle size={14} />}
                        </div>
                        <div>
                          <div className="text-sm font-semibold text-slate-900 leading-tight mb-1">{t.description}</div>
                          <div className="text-[10px] text-slate-400">
                            <span className="font-bold uppercase">{t.createdBy}</span>  {t.category}
                          </div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 text-sm font-semibold text-right text-green-600 whitespace-nowrap">
                      {t.type === 'income' ? formatIDR(t.amount) : '-'}
                    </td>
                    <td className="px-6 py-4 text-sm font-semibold text-right text-red-600 whitespace-nowrap">
                      {t.type === 'expense' ? formatIDR(t.amount) : '-'}
                    </td>
                    <td className="px-6 py-4 text-sm font-bold text-right text-slate-900 whitespace-nowrap">
                      {formatIDR(t.runningBalance || 0)}
                    </td>
                    {isAdmin && (
                      <td className="px-6 py-4 whitespace-nowrap text-center">
                        <div className="flex items-center justify-center">
                          {canModify ? (
                            <button 
                              onClick={() => onEdit(t)} 
                              className="p-2 text-slate-400 hover:text-blue-600 hover:bg-blue-50 rounded-lg"
                            >
                              <Edit3 size={18} />
                            </button>
                          ) : (
                            <span className="text-[10px] text-slate-300 italic">Terkunci</span>
                          )}
                        </div>
                      </td>
                    )}
                  </tr>
                );
              })}
              <tr ref={bottomRef as any} className="h-4 bg-transparent"><td colSpan={isAdmin ? 6 : 5}></td></tr>
            </tbody>
          </table>
        </div>
      </div>

      {!isAdmin && (
        <div className="p-4 flex items-center justify-center space-x-2 text-slate-400 text-[10px] italic">
          <ShieldAlert size={12} />
          <span>Hanya administrator yang dapat mengubah data.</span>
        </div>
      )}
    </div>
  );
};

export default TransactionList;
