
import { GoogleGenAI, Type } from "@google/genai";
import { Transaction, TransactionType } from "./types";

// Always use const ai = new GoogleGenAI({apiKey: process.env.API_KEY});
const ai = new GoogleGenAI({ apiKey: process.env.API_KEY });

export const parseSmartEntry = async (input: string): Promise<Partial<Transaction> | null> => {
  try {
    const response = await ai.models.generateContent({
      model: 'gemini-3-flash-preview',
      contents: `Parse this financial entry: "${input}". 
      Return a JSON object with: 
      - amount (number)
      - description (string)
      - type ('income' or 'expense')
      - category (short string, e.g., 'Food', 'Salary', 'Transport', 'Entertainment')`,
      config: {
        responseMimeType: "application/json",
        responseSchema: {
          type: Type.OBJECT,
          properties: {
            amount: { type: Type.NUMBER },
            description: { type: Type.STRING },
            type: { type: Type.STRING, enum: ['income', 'expense'] },
            category: { type: Type.STRING }
          },
          required: ['amount', 'description', 'type', 'category']
        }
      }
    });

    // response.text is a property, not a method
    return JSON.parse(response.text.trim());
  } catch (error) {
    console.error("Error parsing entry:", error);
    return null;
  }
};

export const getFinancialInsights = async (transactions: Transaction[]): Promise<any> => {
  if (transactions.length === 0) return null;

  const dataStr = JSON.stringify(transactions.slice(-10));
  try {
    const response = await ai.models.generateContent({
      model: 'gemini-3-flash-preview',
      contents: `As a financial advisor, analyze these recent transactions: ${dataStr}. 
      Provide:
      1. A brief analysis of spending patterns.
      2. Three actionable suggestions to improve financial health.
      3. One specific saving tip based on the categories seen.`,
      config: {
        responseMimeType: "application/json",
        responseSchema: {
          type: Type.OBJECT,
          properties: {
            analysis: { type: Type.STRING },
            suggestions: {
              type: Type.ARRAY,
              items: { type: Type.STRING }
            },
            savingTip: { type: Type.STRING }
          },
          required: ['analysis', 'suggestions', 'savingTip']
        }
      }
    });

    // response.text is a property, not a method
    return JSON.parse(response.text.trim());
  } catch (error) {
    console.error("Error fetching insights:", error);
    return null;
  }
};
