
import React, { useState } from 'react';
import { ArrowUpRight, ArrowDownRight, Wallet, TrendingUp, X, ReceiptText, Calendar } from 'lucide-react';
import { CashSummary, Transaction } from '../types';

interface DashboardProps {
  summary: CashSummary;
  transactions: Transaction[];
}

const Dashboard: React.FC<DashboardProps> = ({ summary, transactions }) => {
  const [detailType, setDetailType] = useState<'income' | 'expense' | null>(null);

  const formatIDR = (amount: number) => {
    return new Intl.NumberFormat('id-ID', {
      style: 'currency',
      currency: 'IDR',
      minimumFractionDigits: 0
    }).format(amount);
  };

  const filteredTransactions = transactions
    .filter(t => t.type === detailType)
    .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());

  const closeModal = () => setDetailType(null);

  return (
    <div className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <div className="bg-white p-8 rounded-3xl border border-slate-100 shadow-sm relative overflow-hidden">
          <div className="absolute top-0 right-0 p-4 opacity-5 pointer-events-none">
            <Wallet size={100} className="text-blue-600" />
          </div>
          <p className="text-xs font-bold text-slate-400 uppercase tracking-widest mb-1">Total Saldo Kas</p>
          <h3 className="text-3xl font-black text-slate-900">{formatIDR(summary.balance)}</h3>
          <div className="mt-4 flex items-center text-[10px] font-bold text-blue-600 bg-blue-50 w-fit px-3 py-1 rounded-full border border-blue-100">
             <TrendingUp size={12} className="mr-1.5" /> Akumulasi Real-time
          </div>
        </div>

        <button 
          onClick={() => setDetailType('income')}
          className="bg-white p-8 rounded-3xl border border-slate-100 shadow-sm group hover:border-green-200 transition-all text-left"
        >
          <div className="flex items-center justify-between mb-2">
            <p className="text-xs font-bold text-slate-400 uppercase tracking-widest">Pemasukan</p>
            <ArrowUpRight className="text-green-500" size={20} />
          </div>
          <h3 className="text-2xl font-bold text-slate-900">{formatIDR(summary.totalIncome)}</h3>
          <p className="text-[10px] text-green-600 mt-2 font-bold opacity-0 group-hover:opacity-100 transition-opacity">Lihat Rincian</p>
        </button>

        <button 
          onClick={() => setDetailType('expense')}
          className="bg-white p-8 rounded-3xl border border-slate-100 shadow-sm group hover:border-red-200 transition-all text-left"
        >
          <div className="flex items-center justify-between mb-2">
            <p className="text-xs font-bold text-slate-400 uppercase tracking-widest">Pengeluaran</p>
            <ArrowDownRight className="text-red-500" size={20} />
          </div>
          <h3 className="text-2xl font-bold text-slate-900">{formatIDR(summary.totalExpense)}</h3>
          <p className="text-[10px] text-red-600 mt-2 font-bold opacity-0 group-hover:opacity-100 transition-opacity">Lihat Rincian</p>
        </button>
      </div>

      <div className="bg-blue-600 p-8 rounded-3xl text-white shadow-xl shadow-blue-100 relative overflow-hidden">
        <h4 className="text-xl font-bold mb-2 uppercase tracking-tight">Manajemen Kas Pintar</h4>
        <p className="text-blue-100 text-sm max-w-xl leading-relaxed">
          Aplikasi ini mendistribusikan data secara real-time ke semua perangkat admin. Gunakan Cloud Sync untuk sinkronisasi antar perangkat.
        </p>
      </div>

      {detailType && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-slate-900/60 backdrop-blur-sm">
          <div className="bg-white w-full max-w-2xl rounded-3xl shadow-2xl overflow-hidden flex flex-col max-h-[80vh]">
            <div className="p-6 border-b flex items-center justify-between">
              <h3 className="text-xl font-bold">Rincian {detailType === 'income' ? 'Pemasukan' : 'Pengeluaran'}</h3>
              <button onClick={closeModal} className="p-2 text-slate-400 hover:text-slate-600 rounded-xl">
                <X size={24} />
              </button>
            </div>
            <div className="flex-1 overflow-y-auto p-6 space-y-3">
              {filteredTransactions.map((t) => (
                <div key={t.id} className="flex items-center justify-between p-4 rounded-2xl bg-slate-50 border border-slate-100">
                  <div>
                    <p className="text-sm font-bold text-slate-900">{t.description}</p>
                    <p className="text-[10px] text-slate-400 font-bold uppercase mt-1">
                      {new Date(t.date).toLocaleDateString('id-ID', { day: 'numeric', month: 'short' })}  {t.category}
                    </p>
                  </div>
                  <div className={`text-sm font-black ${detailType === 'income' ? 'text-green-600' : 'text-red-600'}`}>
                    {detailType === 'income' ? '+' : '-'} {formatIDR(t.amount)}
                  </div>
                </div>
              ))}
            </div>
            <div className="p-4 bg-slate-50 border-t flex justify-end">
              <button onClick={closeModal} className="px-6 py-2 bg-slate-900 text-white rounded-xl font-bold">Tutup</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Dashboard;
