
import React, { useState } from 'react';
import { 
  ShieldCheck, 
  UserCog, 
  Key, 
  Info, 
  Trash2, 
  Layout,
  Cloud,
  Copy,
  Check,
  UserPlus,
  Edit2,
  Save,
  X as CloseIcon
} from 'lucide-react';
import { Admin, ActivityLog } from '../types';

interface AdminSettingsProps {
  appName: string;
  onUpdateAppName: (name: string) => void;
  admins: Admin[];
  currentAdmin: Admin;
  onUpdateAdmins: (updatedAdmins: Admin[]) => void;
  googleSheetsUrl: string;
  onUpdateGsUrl: (url: string) => void;
  addLog: (action: ActivityLog['action'], details: string) => void;
  onResetData: () => void;
}

type TabType = 'general' | 'security' | 'sync' | 'admin';

const AdminSettings: React.FC<AdminSettingsProps> = ({ 
  appName,
  onUpdateAppName,
  admins, 
  currentAdmin, 
  onUpdateAdmins, 
  googleSheetsUrl,
  onUpdateGsUrl,
  addLog,
  onResetData
}) => {
  const [activeTab, setActiveTab] = useState<TabType>('general');
  const [isEditingOwnName, setIsEditingOwnName] = useState(false);
  const [ownNameInput, setOwnNameInput] = useState(currentAdmin.name);
  const [newAdminName, setNewAdminName] = useState('');
  const [newAdminPassword, setNewAdminPassword] = useState('');
  const [passwordForm, setPasswordForm] = useState({ current: '', new: '', confirm: '' });
  const [message, setMessage] = useState<{ text: string, type: 'success' | 'error' } | null>(null);
  const [showResetConfirm, setShowResetConfirm] = useState(false);
  const [confirmPassword, setConfirmPassword] = useState('');
  const [copied, setCopied] = useState(false);

  const isSuperAdmin = currentAdmin.isSuperAdmin;

  // SCRIPT VERSI 3.1
  const scriptCode = `function doGet(e) {
  var ss = SpreadsheetApp.getActiveSpreadsheet();
  var sheet = ss.getSheetByName("Data_SmartKas") || ss.insertSheet("Data_SmartKas");
  var metadata = JSON.parse(PropertiesService.getScriptProperties().getProperty("metadata") || '{"appName":"SmartKas"}');
  
  var data = sheet.getDataRange().getValues();
  var transactions = [];
  
  for (var i = 1; i < data.length; i++) {
    if (data[i][7]) { 
      var debet = Number(data[i][2]) || 0;
      var kredit = Number(data[i][3]) || 0;
      transactions.push({
        id: String(data[i][7]),
        date: String(data[i][0]),
        description: String(data[i][1]),
        amount: debet > 0 ? debet : kredit,
        type: debet > 0 ? 'income' : 'expense',
        category: String(data[i][5]),
        createdBy: String(data[i][6])
      });
    }
  }
  return ContentService.createTextOutput(JSON.stringify({appName: metadata.appName, transactions: transactions})).setMimeType(ContentService.MimeType.JSON);
}

function doPost(e) {
  try {
    var payload = JSON.parse(e.postData.contents);
    var ss = SpreadsheetApp.getActiveSpreadsheet();
    var sheet = ss.getSheetByName("Data_SmartKas") || ss.insertSheet("Data_SmartKas");
    
    PropertiesService.getScriptProperties().setProperty("metadata", JSON.stringify({appName: payload.appName}));
    
    sheet.clear();
    var headers = ["Tanggal", "Keterangan", "Debet (Masuk)", "Kredit (Keluar)", "Saldo", "Kategori", "Input Oleh", "ID"];
    sheet.appendRow(headers);
    sheet.getRange(1, 1, 1, headers.length).setBackground("#1e40af").setFontColor("white").setFontWeight("bold").setVerticalAlignment("middle").setHorizontalAlignment("center");
    
    if (payload.transactions && payload.transactions.length > 0) {
      var runningBalance = 0;
      var rows = payload.transactions.map(function(t) {
        var debet = t.type === 'income' ? t.amount : 0;
        var kredit = t.type === 'expense' ? t.amount : 0;
        runningBalance += (debet - kredit);
        return [t.date, t.description, debet, kredit, runningBalance, t.category, t.createdBy, t.id];
      });
      
      var range = sheet.getRange(2, 1, rows.length, 8);
      range.setValues(rows);
      sheet.getRange(2, 3, rows.length, 3).setNumberFormat("#,##0");
      sheet.setFrozenRows(1);
      sheet.autoResizeColumns(1, 8);
    }
    SpreadsheetApp.flush();
    return ContentService.createTextOutput("OK").setMimeType(ContentService.MimeType.TEXT);
  } catch (err) {
    return ContentService.createTextOutput("ERROR: " + err.message).setMimeType(ContentService.MimeType.TEXT);
  }
}`;

  const handleCopy = () => {
    navigator.clipboard.writeText(scriptCode);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const handleUpdateOwnName = () => {
    if (!ownNameInput.trim()) return;
    const updatedAdmins = admins.map(a => 
      a.id === currentAdmin.id ? { ...a, name: ownNameInput.trim() } : a
    );
    onUpdateAdmins(updatedAdmins);
    addLog('UPDATE', `Mengubah nama profil sendiri dari ${currentAdmin.name} menjadi ${ownNameInput}`);
    setIsEditingOwnName(false);
    setMessage({ text: 'Nama profil berhasil diperbarui.', type: 'success' });
    setTimeout(() => setMessage(null), 3000);
  };

  const handleChangePassword = (e: React.FormEvent) => {
    e.preventDefault();
    if (passwordForm.current !== currentAdmin.password) { 
      setMessage({ text: 'Sandi lama tidak sesuai.', type: 'error' }); 
      return; 
    }
    if (passwordForm.new !== passwordForm.confirm) { 
      setMessage({ text: 'Konfirmasi sandi baru tidak cocok.', type: 'error' }); 
      return; 
    }
    onUpdateAdmins(admins.map(a => a.id === currentAdmin.id ? { ...a, password: passwordForm.new } : a));
    setPasswordForm({ current: '', new: '', confirm: '' });
    setMessage({ text: 'Kata sandi berhasil diperbarui.', type: 'success' });
    addLog('UPDATE', 'Mengubah kata sandi admin');
    setTimeout(() => setMessage(null), 3000);
  };

  const handleAddAdmin = (e: React.FormEvent) => {
    e.preventDefault();
    if (!newAdminName || !newAdminPassword) return;
    const newAdmin: Admin = {
      id: crypto.randomUUID(),
      name: newAdminName,
      password: newAdminPassword,
      isSuperAdmin: false
    };
    onUpdateAdmins([...admins, newAdmin]);
    addLog('CREATE', `Menambah admin baru: ${newAdminName}`);
    setNewAdminName('');
    setNewAdminPassword('');
    setMessage({ text: `Admin ${newAdminName} berhasil ditambahkan.`, type: 'success' });
    setTimeout(() => setMessage(null), 3000);
  };

  const TabButton = ({ id, icon: Icon, label }: { id: TabType, icon: any, label: string }) => (
    <button
      onClick={() => setActiveTab(id)}
      className={`flex items-center space-x-3 px-4 py-3 rounded-xl transition-all w-full text-left ${
        activeTab === id ? 'bg-blue-600 text-white shadow-md' : 'text-slate-600 hover:bg-slate-100'
      }`}
    >
      <Icon size={18} />
      <span className="font-semibold text-sm">{label}</span>
    </button>
  );

  return (
    <div className="max-w-5xl animate-in fade-in duration-500">
      {message && (
        <div className={`mb-6 p-4 rounded-2xl flex items-center space-x-3 shadow-sm animate-in slide-in-from-top-2 duration-300 ${message.type === 'success' ? 'bg-green-50 text-green-700 border border-green-100' : 'bg-red-50 text-red-700 border border-red-100'}`}>
          <Info size={20} />
          <span className="font-medium text-sm">{message.text}</span>
        </div>
      )}

      <div className="flex flex-col md:flex-row gap-8">
        <div className="w-full md:w-64 shrink-0">
          <div className="bg-white p-4 rounded-3xl border border-slate-100 shadow-sm space-y-1 sticky top-4">
            <TabButton id="general" icon={Layout} label="Identitas" />
            <TabButton id="security" icon={Key} label="Keamanan" />
            <TabButton id="sync" icon={Cloud} label="Cloud" />
            {isSuperAdmin && <TabButton id="admin" icon={UserCog} label="Manajemen Admin" />}
          </div>
        </div>

        <div className="flex-1 space-y-6">
          {activeTab === 'general' && (
            <div className="bg-white p-8 rounded-3xl border border-slate-100 shadow-sm animate-in slide-in-from-bottom-2 duration-300">
              <h3 className="text-xl font-bold mb-4 flex items-center gap-2"><Layout size={20} className="text-blue-600" /> Nama Aplikasi</h3>
              <p className="text-sm text-slate-500 mb-6">Ubah identitas buku kas keuangan Anda.</p>
              <input type="text" value={appName} onChange={(e) => onUpdateAppName(e.target.value)} className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl outline-none focus:ring-2 focus:ring-blue-500" />
            </div>
          )}

          {activeTab === 'security' && (
            <div className="bg-white p-8 rounded-3xl border border-slate-100 shadow-sm animate-in slide-in-from-bottom-2 duration-300">
              <h3 className="text-xl font-bold mb-6 flex items-center gap-2"><Key size={20} className="text-orange-500" /> Ganti Kata Sandi</h3>
              <form onSubmit={handleChangePassword} className="space-y-4 max-w-sm">
                <input required type="password" placeholder="Sandi Lama" value={passwordForm.current} onChange={(e) => setPasswordForm({...passwordForm, current: e.target.value})} className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl outline-none focus:ring-2 focus:ring-blue-500" />
                <input required type="password" placeholder="Sandi Baru" value={passwordForm.new} onChange={(e) => setPasswordForm({...passwordForm, new: e.target.value})} className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl outline-none focus:ring-2 focus:ring-blue-500" />
                <input required type="password" placeholder="Konfirmasi Sandi Baru" value={passwordForm.confirm} onChange={(e) => setPasswordForm({...passwordForm, confirm: e.target.value})} className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl outline-none focus:ring-2 focus:ring-blue-500" />
                <button type="submit" className="w-full bg-slate-900 text-white py-4 rounded-2xl font-bold hover:bg-slate-800 transition-all">Simpan Perubahan</button>
              </form>
            </div>
          )}

          {activeTab === 'sync' && (
            <div className="space-y-6 animate-in slide-in-from-bottom-2 duration-300">
              <div className="bg-white p-8 rounded-3xl border shadow-sm space-y-6">
                <h3 className="text-xl font-bold flex items-center gap-2"><Cloud size={24} className="text-blue-600"/> Sinkronisasi Cloud v3.1</h3>
                <div>
                  <label className="text-xs font-bold text-slate-400 uppercase block mb-2">Google Apps Script Web App URL</label>
                  <input type="text" placeholder="https://script.google.com/macros/s/.../exec" value={googleSheetsUrl} onChange={(e) => onUpdateGsUrl(e.target.value)} className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl font-mono text-xs outline-none focus:ring-2 focus:ring-blue-500" />
                </div>
                <div className="border-t pt-6">
                  <div className="flex items-center justify-between mb-4">
                    <h4 className="font-bold text-slate-800">Kode Apps Script Terbaru</h4>
                    <button onClick={handleCopy} className="flex items-center gap-2 px-4 py-2 bg-blue-50 text-blue-600 rounded-lg text-xs font-bold hover:bg-blue-100 transition-all">
                      {copied ? <Check size={14}/> : <Copy size={14}/>} {copied ? 'Berhasil Salin' : 'Salin Kode'}
                    </button>
                  </div>
                  <pre className="bg-slate-50 border p-4 rounded-xl text-[10px] text-slate-400 overflow-x-auto max-h-40 custom-scrollbar">{scriptCode}</pre>
                </div>
              </div>
            </div>
          )}

          {activeTab === 'admin' && isSuperAdmin && (
            <div className="space-y-6 animate-in slide-in-from-bottom-2 duration-300">
              <div className="bg-white p-8 rounded-3xl border border-slate-100 shadow-sm">
                <h3 className="text-xl font-bold mb-6 flex items-center gap-2"><UserPlus size={20} className="text-green-600" /> Tambah Admin Baru</h3>
                <form onSubmit={handleAddAdmin} className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-1">
                    <label className="text-[10px] font-black text-slate-400 uppercase tracking-widest px-1">Nama Admin</label>
                    <input required type="text" placeholder="Contoh: Admin Keuangan" value={newAdminName} onChange={(e) => setNewAdminName(e.target.value)} className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl outline-none focus:ring-2 focus:ring-blue-500" />
                  </div>
                  <div className="space-y-1">
                    <label className="text-[10px] font-black text-slate-400 uppercase tracking-widest px-1">Kata Sandi</label>
                    <input required type="password" placeholder="" value={newAdminPassword} onChange={(e) => setNewAdminPassword(e.target.value)} className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl outline-none focus:ring-2 focus:ring-blue-500" />
                  </div>
                  <div className="md:col-span-2 pt-2">
                    <button type="submit" className="w-full bg-blue-600 text-white py-4 rounded-2xl font-bold hover:bg-blue-700 transition-all shadow-lg shadow-blue-100">Tambah Admin</button>
                  </div>
                </form>
              </div>

              <div className="bg-white p-8 rounded-3xl border border-slate-100 shadow-sm">
                <h3 className="text-xl font-bold mb-6 flex items-center gap-2"><UserCog size={20} className="text-blue-600" /> Daftar Admin Terdaftar</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {admins.map((admin) => (
                    <div key={admin.id} className="flex items-center justify-between p-4 bg-slate-50 border border-slate-100 rounded-2xl group hover:border-blue-200 transition-colors">
                      <div className="flex items-center space-x-3 w-full">
                        <div className={`p-2 rounded-xl shrink-0 ${admin.isSuperAdmin ? 'bg-blue-600 text-white shadow-md' : 'bg-white text-slate-400 border border-slate-100'}`}>
                          <ShieldCheck size={18} />
                        </div>
                        <div className="flex-1 min-w-0">
                          {isEditingOwnName && admin.id === currentAdmin.id ? (
                            <div className="flex items-center gap-2">
                              <input 
                                autoFocus
                                type="text" 
                                className="w-full px-2 py-1 text-sm bg-white border border-blue-300 rounded outline-none focus:ring-1 focus:ring-blue-500"
                                value={ownNameInput}
                                onChange={(e) => setOwnNameInput(e.target.value)}
                                onKeyDown={(e) => e.key === 'Enter' && handleUpdateOwnName()}
                              />
                              <button onClick={handleUpdateOwnName} className="p-1 text-green-600 hover:bg-green-50 rounded"><Save size={14}/></button>
                              <button onClick={() => setIsEditingOwnName(false)} className="p-1 text-slate-400 hover:bg-slate-50 rounded"><CloseIcon size={14}/></button>
                            </div>
                          ) : (
                            <div className="flex items-center gap-2 group/name">
                              <p className="font-bold text-slate-700 truncate">{admin.name}</p>
                              {admin.id === currentAdmin.id && (
                                <button 
                                  onClick={() => { setIsEditingOwnName(true); setOwnNameInput(admin.name); }} 
                                  className="p-1 text-slate-300 hover:text-blue-600 opacity-0 group-hover/name:opacity-100 transition-opacity"
                                  title="Ubah nama saya"
                                >
                                  <Edit2 size={12} />
                                </button>
                              )}
                            </div>
                          )}
                          <p className="text-[9px] font-black text-slate-400 uppercase tracking-tighter">{admin.isSuperAdmin ? 'Admin Utama' : 'Admin Staff'}</p>
                        </div>
                      </div>
                      {!admin.isSuperAdmin && (
                        <button 
                          onClick={() => {
                            if(window.confirm(`Hapus admin ${admin.name}?`)) {
                              onUpdateAdmins(admins.filter(a => a.id !== admin.id));
                              addLog('DELETE', `Menghapus admin: ${admin.name}`);
                            }
                          }} 
                          className="p-2.5 text-slate-300 hover:text-red-600 hover:bg-red-50 rounded-xl transition-all shrink-0"
                        >
                          <Trash2 size={18} />
                        </button>
                      )}
                    </div>
                  ))}
                </div>
              </div>

              <div className="bg-red-50 p-8 rounded-3xl border border-red-100 flex flex-col md:flex-row items-center justify-between gap-6">
                <div className="text-center md:text-left">
                  <h4 className="font-bold text-red-900 text-lg">Hapus Seluruh Data</h4>
                  <p className="text-xs text-red-600 mt-1">Ini akan menghapus semua riwayat transaksi, log, dan pengaturan cloud secara permanen.</p>
                </div>
                <button onClick={() => setShowResetConfirm(true)} className="w-full md:w-auto px-10 py-4 bg-red-600 text-white rounded-2xl font-bold hover:bg-red-700 shadow-xl shadow-red-100 transition-all">Reset Total</button>
              </div>
            </div>
          )}
        </div>
      </div>

      {showResetConfirm && (
        <div className="fixed inset-0 z-[110] flex items-center justify-center p-4 bg-slate-900/60 backdrop-blur-sm animate-in fade-in duration-300">
          <div className="bg-white w-full max-w-sm rounded-3xl p-8 text-center space-y-4 shadow-2xl animate-in zoom-in duration-300">
            <div className="mx-auto w-16 h-16 bg-red-100 text-red-600 rounded-full flex items-center justify-center mb-2">
              <Trash2 size={32} />
            </div>
            <h3 className="text-xl font-bold text-slate-900 tracking-tight">Konfirmasi Reset Total</h3>
            <p className="text-sm text-slate-500">Tindakan ini tidak dapat dibatalkan. Masukkan kata sandi Admin Utama untuk melanjutkan.</p>
            <form onSubmit={(e) => { e.preventDefault(); if(confirmPassword === currentAdmin.password) { onResetData(); setShowResetConfirm(false); } else { alert('Kata sandi salah!'); } }} className="space-y-4">
              <input autoFocus type="password" placeholder="Kata Sandi Admin" className="w-full px-5 py-4 bg-slate-50 border border-slate-200 rounded-2xl text-center outline-none focus:ring-2 focus:ring-red-500" value={confirmPassword} onChange={(e) => setConfirmPassword(e.target.value)} />
              <div className="grid grid-cols-2 gap-3">
                <button type="button" onClick={() => setShowResetConfirm(false)} className="py-4 bg-slate-100 rounded-2xl font-bold text-slate-600 hover:bg-slate-200 transition-all">Batal</button>
                <button type="submit" className="py-4 bg-red-600 text-white rounded-2xl font-bold hover:bg-red-700 shadow-lg shadow-red-100 transition-all">Ya, Reset</button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default AdminSettings;
