
import React, { useState, useEffect, useMemo, useCallback } from 'react';
import { 
  Plus, 
  LayoutDashboard, 
  Receipt, 
  Sparkles,
  Wallet,
  Menu,
  X,
  Lock,
  Unlock,
  Search,
  KeyRound,
  History,
  UserCheck,
  Settings,
  RefreshCw,
  Wifi,
  WifiOff
} from 'lucide-react';
import { Transaction, CashSummary, ActivityLog, Admin } from './types';
import Dashboard from './components/Dashboard';
import TransactionList from './components/TransactionList';
import TransactionForm from './components/TransactionForm';
import SmartInsights from './components/SmartInsights';
import ActivityLogList from './components/ActivityLogList';
import AdminSettings from './components/AdminSettings';

const DEFAULT_ADMINS: Admin[] = [
  { id: 'admin1', name: 'Admin Utama', password: 'admin', isSuperAdmin: true },
];

const App: React.FC = () => {
  const [appName, setAppName] = useState(() => localStorage.getItem('smartkas_app_name') || 'SmartKas');
  const [admins, setAdmins] = useState<Admin[]>(() => {
    const saved = localStorage.getItem('smartkas_admins');
    return saved ? JSON.parse(saved) : DEFAULT_ADMINS;
  });
  const [transactions, setTransactions] = useState<Transaction[]>(() => {
    const saved = localStorage.getItem('smartkas_transactions');
    try { return saved ? JSON.parse(saved) : []; } catch (e) { return []; }
  });
  const [logs, setLogs] = useState<ActivityLog[]>(() => {
    const saved = localStorage.getItem('smartkas_logs');
    try { return saved ? JSON.parse(saved) : []; } catch (e) { return []; }
  });
  const [googleSheetsUrl, setGoogleSheetsUrl] = useState<string>(() => localStorage.getItem('smartkas_gs_url') || '');

  const [isSyncing, setIsSyncing] = useState(false);
  const [lastSynced, setLastSynced] = useState<string | null>(null);
  const [view, setView] = useState<'dashboard' | 'transactions' | 'insights' | 'logs' | 'settings'>('dashboard');
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [editingTransaction, setEditingTransaction] = useState<Transaction | null>(null);
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [loggedAdminId, setLoggedAdminId] = useState<string | null>(() => sessionStorage.getItem('smartkas_logged_admin_id'));

  const currentAdmin = useMemo(() => admins.find(a => a.id === loggedAdminId), [admins, loggedAdminId]);
  const isCurrentAdminSuperAdmin = useMemo(() => currentAdmin?.isSuperAdmin || false, [currentAdmin]);

  const fetchCloudData = useCallback(async () => {
    if (!googleSheetsUrl) return;
    setIsSyncing(true);
    try {
      const response = await fetch(`${googleSheetsUrl}?t=${Date.now()}`);
      const cloudData = await response.json();
      if (cloudData && Array.isArray(cloudData.transactions)) {
        setTransactions(cloudData.transactions);
        if (cloudData.appName) setAppName(cloudData.appName);
        setLastSynced(new Date().toLocaleTimeString('id-ID', { hour: '2-digit', minute: '2-digit' }));
      }
    } catch (e) { console.error(e); } finally { setIsSyncing(false); }
  }, [googleSheetsUrl]);

  const performSync = useCallback(async (dataToSync: Transaction[], nameToSync: string) => {
    if (!googleSheetsUrl) return;
    setIsSyncing(true);
    try {
      await fetch(googleSheetsUrl, {
        method: 'POST',
        mode: 'no-cors',
        body: JSON.stringify({
          appName: nameToSync,
          transactions: dataToSync.sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime()),
          timestamp: new Date().toISOString()
        })
      });
      setLastSynced(new Date().toLocaleTimeString('id-ID', { hour: '2-digit', minute: '2-digit' }));
    } catch (e) { console.error(e); } finally { setIsSyncing(false); }
  }, [googleSheetsUrl]);

  useEffect(() => {
    if (googleSheetsUrl) fetchCloudData();
  }, [googleSheetsUrl, fetchCloudData]);

  useEffect(() => {
    localStorage.setItem('smartkas_transactions', JSON.stringify(transactions));
    localStorage.setItem('smartkas_app_name', appName);
    localStorage.setItem('smartkas_logs', JSON.stringify(logs));
    localStorage.setItem('smartkas_admins', JSON.stringify(admins));
    localStorage.setItem('smartkas_gs_url', googleSheetsUrl);
    if (googleSheetsUrl && currentAdmin) {
      const timer = setTimeout(() => performSync(transactions, appName), 3000);
      return () => clearTimeout(timer);
    }
  }, [transactions, appName, logs, admins, googleSheetsUrl, currentAdmin, performSync]);

  const addLog = useCallback((action: ActivityLog['action'], details: string) => {
    const newLog: ActivityLog = { id: crypto.randomUUID(), timestamp: new Date().toISOString(), adminName: currentAdmin?.name || 'Unknown', action, details };
    setLogs(prev => [newLog, ...prev].slice(0, 200));
  }, [currentAdmin]);

  const summary = useMemo<CashSummary>(() => {
    const income = transactions.filter(t => t.type === 'income').reduce((sum, t) => sum + t.amount, 0);
    const expense = transactions.filter(t => t.type === 'expense').reduce((sum, t) => sum + t.amount, 0);
    return { totalIncome: income, totalExpense: expense, balance: income - expense };
  }, [transactions]);

  const transactionsWithBalance = useMemo(() => {
    // Urutkan dari terlama ke terbaru (Ascending)
    const sortedAsc = [...transactions].sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime());
    let currentBalance = 0;
    const mapped = sortedAsc.map(t => {
      if (t.type === 'income') currentBalance += t.amount; else currentBalance -= t.amount;
      return { ...t, runningBalance: currentBalance };
    });
    // Filter berdasarkan pencarian, tapi biarkan urutan terlama di atas (tidak di-reverse)
    return mapped.filter(t => 
      t.description.toLowerCase().includes(searchQuery.toLowerCase()) || 
      t.category.toLowerCase().includes(searchQuery.toLowerCase())
    );
  }, [transactions, searchQuery]);

  const handleAddTransaction = (newT: Omit<Transaction, 'id' | 'createdBy'>) => {
    if (!currentAdmin) return;
    const transaction: Transaction = { ...newT, id: crypto.randomUUID(), createdBy: currentAdmin.name, updatedAt: new Date().toISOString() };
    setTransactions(prev => [...prev, transaction]);
    addLog('CREATE', `Tambah: ${transaction.description}`);
    setIsFormOpen(false);
  };

  const handleUpdateTransaction = (updated: Omit<Transaction, 'id' | 'createdBy'>) => {
    if (!editingTransaction || !currentAdmin) return;
    setTransactions(prev => prev.map(t => t.id === editingTransaction.id ? { ...t, ...updated, updatedBy: currentAdmin.name, updatedAt: new Date().toISOString() } : t));
    addLog('UPDATE', `Ubah: ${editingTransaction.description}`);
    setEditingTransaction(null);
    setIsFormOpen(false);
  };

  const handleDeleteTransaction = (id: string) => {
    if (!currentAdmin) return;
    const target = transactions.find(t => t.id === id);
    if (!target) return;
    if (!isCurrentAdminSuperAdmin && target.createdBy !== currentAdmin.name) { alert('Akses Ditolak'); return; }
    if (window.confirm('Hapus transaksi?')) { setTransactions(prev => prev.filter(t => t.id !== id)); addLog('DELETE', `Hapus: ${target.description}`); }
  };

  const NavItem = ({ id, icon: Icon, label }: { id: typeof view, icon: any, label: string }) => (
    <button
      onClick={() => { setView(id); setIsSidebarOpen(false); }}
      className={`flex items-center space-x-3 w-full px-4 py-3 rounded-xl transition-all ${
        view === id ? 'bg-blue-600 text-white shadow-lg' : 'text-slate-600 hover:bg-slate-100'
      }`}
    >
      <Icon size={20} />
      <span className="font-medium">{label}</span>
    </button>
  );

  return (
    <div className="min-h-screen bg-slate-50 flex flex-col md:flex-row text-slate-900">
      {/* Mobile Header */}
      <div className="md:hidden bg-white border-b px-4 py-4 flex items-center justify-between sticky top-0 z-30">
        <div className="flex items-center space-x-2">
          <div className="bg-blue-600 p-2 rounded-lg text-white"><Wallet size={20} /></div>
          <h1 className="text-xl font-bold">{appName}</h1>
        </div>
        <div className="flex items-center gap-3">
          {googleSheetsUrl && (
            <button onClick={fetchCloudData} className={`text-slate-400 ${isSyncing ? 'animate-spin text-blue-600' : ''}`}>
              <RefreshCw size={20} />
            </button>
          )}
          <button onClick={() => setIsSidebarOpen(!isSidebarOpen)} className="p-2 text-slate-600">
            {isSidebarOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>
      </div>

      {/* Sidebar */}
      <aside className={`fixed inset-y-0 left-0 z-40 w-64 bg-white border-r transform transition-transform duration-300 md:relative md:translate-x-0 ${isSidebarOpen ? 'translate-x-0' : '-translate-x-full'}`}>
        <div className="p-6 h-full flex flex-col">
          <div className="hidden md:flex items-center space-x-3 mb-10">
            <div className="bg-blue-600 p-2 rounded-xl text-white shadow-lg"><Wallet size={24} /></div>
            <div className="overflow-hidden">
              <h1 className="text-2xl font-bold text-slate-900 truncate">{appName}</h1>
              {currentAdmin && <div className="text-[10px] font-bold text-blue-600 uppercase flex items-center mt-1"><UserCheck size={10} className="mr-1"/> {currentAdmin.name}</div>}
            </div>
          </div>
          <nav className="space-y-2 flex-1">
            <NavItem id="dashboard" icon={LayoutDashboard} label="Dashboard" />
            <NavItem id="transactions" icon={Receipt} label="Transaksi" />
            <NavItem id="insights" icon={Sparkles} label="AI Insights" />
            {currentAdmin && <NavItem id="logs" icon={History} label="Log Aktivitas" />}
            {currentAdmin && <NavItem id="settings" icon={Settings} label="Pengaturan" />}
          </nav>

          <div className="pt-6 border-t mt-auto space-y-3">
            <div className="p-4 rounded-2xl bg-slate-50 border border-slate-100">
               <div className="flex items-center justify-between mb-2">
                 <span className="text-[10px] font-bold text-slate-400 uppercase">Cloud Status</span>
                 {googleSheetsUrl ? (
                   <div className="flex items-center text-green-500 gap-1">
                     <span className="text-[9px] font-bold">ONLINE</span>
                     <Wifi size={10} />
                   </div>
                 ) : (
                   <div className="flex items-center text-slate-400 gap-1">
                     <span className="text-[9px] font-bold">LOCAL</span>
                     <WifiOff size={10} />
                   </div>
                 )}
               </div>
               {googleSheetsUrl && (
                 <button 
                   onClick={fetchCloudData} 
                   disabled={isSyncing}
                   className="w-full py-2 bg-white border border-slate-200 rounded-lg text-[10px] font-bold flex items-center justify-center gap-2 hover:bg-slate-50 transition-colors"
                 >
                   <RefreshCw size={10} className={isSyncing ? 'animate-spin' : ''} />
                   Sync Sekarang
                 </button>
               )}
               {lastSynced && <p className="text-[9px] text-slate-400 text-center mt-2 italic">Terakhir: {lastSynced}</p>}
            </div>

            <button 
              onClick={() => { if(currentAdmin) { setLoggedAdminId(null); sessionStorage.removeItem('smartkas_logged_admin_id'); } else { window.dispatchEvent(new CustomEvent('open-login')); } }} 
              className={`flex items-center space-x-3 w-full px-4 py-3 rounded-xl transition-all ${loggedAdminId ? 'text-orange-600 hover:bg-orange-50' : 'text-slate-600 hover:bg-slate-100'}`}
            >
              {loggedAdminId ? <Lock size={20} /> : <Unlock size={20} />}
              <span className="font-medium text-sm">{loggedAdminId ? 'Logout' : 'Login Admin'}</span>
            </button>
          </div>
        </div>
      </aside>

      {/* Main Content */}
      <main className="flex-1 overflow-y-auto p-4 md:p-8">
        <header className="flex flex-col md:flex-row md:items-center justify-between mb-8 gap-4">
          <div>
            <h2 className="text-2xl font-bold text-slate-800 uppercase tracking-tight">
              {view === 'dashboard' && 'Dashboard'}
              {view === 'transactions' && 'Riwayat Transaksi'}
              {view === 'insights' && 'Analisis AI'}
              {view === 'logs' && 'Log Aktivitas'}
              {view === 'settings' && 'Pengaturan'}
            </h2>
            <p className="text-slate-500 text-sm">{currentAdmin ? `Administrator: ${currentAdmin.name}` : 'Akses Tamu (Mode Online)'}</p>
          </div>
          {currentAdmin && (
            <button onClick={() => { setEditingTransaction(null); setIsFormOpen(true); }} className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-xl shadow-lg font-bold flex items-center justify-center space-x-2 transition-all active:scale-95">
              <Plus size={20} /> <span>Transaksi Baru</span>
            </button>
          )}
        </header>

        <div className="space-y-6">
          {view === 'dashboard' && <Dashboard summary={summary} transactions={transactions} />}
          {view === 'transactions' && (
            <div className="space-y-4">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400" size={18} />
                <input type="text" placeholder="Cari transaksi..." className="w-full pl-10 pr-4 py-3 bg-white border border-slate-200 rounded-xl outline-none focus:ring-2 focus:ring-blue-500 transition-all" value={searchQuery} onChange={(e) => setSearchQuery(e.target.value)} />
              </div>
              <TransactionList transactions={transactionsWithBalance} onEdit={(t) => { setEditingTransaction(t); setIsFormOpen(true); }} isAdmin={!!currentAdmin} isSuperAdmin={isCurrentAdminSuperAdmin} loggedAdminName={currentAdmin?.name} onDelete={handleDeleteTransaction} />
            </div>
          )}
          {view === 'insights' && <SmartInsights transactions={transactions} />}
          {view === 'logs' && currentAdmin && <ActivityLogList logs={logs} />}
          {view === 'settings' && currentAdmin && (
            <AdminSettings appName={appName} onUpdateAppName={setAppName} admins={admins} currentAdmin={currentAdmin} onUpdateAdmins={setAdmins} googleSheetsUrl={googleSheetsUrl} onUpdateGsUrl={setGoogleSheetsUrl} addLog={addLog} onResetData={() => { setTransactions([]); setLogs([]); setAppName('SmartKas'); setGoogleSheetsUrl(''); window.location.reload(); }} />
          )}
        </div>
      </main>

      <LoginModal admins={admins} onLoginSuccess={(admin) => { setLoggedAdminId(admin.id); sessionStorage.setItem('smartkas_logged_admin_id', admin.id); addLog('UPDATE', `Login admin: ${admin.name}`); }} />
      {isFormOpen && currentAdmin && (
        <TransactionForm onClose={() => { setIsFormOpen(false); setEditingTransaction(null); }} onSubmit={editingTransaction ? handleUpdateTransaction : handleAddTransaction} initialData={editingTransaction || undefined} />
      )}
    </div>
  );
};

const LoginModal = ({ admins, onLoginSuccess }: { admins: Admin[], onLoginSuccess: (a: Admin) => void }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [selectedId, setSelectedId] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(false);

  useEffect(() => {
    const handleOpen = () => { setSelectedId(admins[0]?.id || ''); setPassword(''); setError(false); setIsOpen(true); };
    window.addEventListener('open-login', handleOpen);
    return () => window.removeEventListener('open-login', handleOpen);
  }, [admins]);

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 z-[100] flex items-center justify-center p-4 bg-slate-900/60 backdrop-blur-sm">
      <div className="bg-white w-full max-w-sm rounded-3xl shadow-2xl p-8 animate-in zoom-in duration-300">
        <KeyRound size={48} className="mx-auto text-blue-600 mb-4" />
        <h3 className="text-2xl font-bold text-center mb-6 text-slate-900">Login Admin</h3>
        <form onSubmit={(e) => { e.preventDefault(); const admin = admins.find(a => a.id === selectedId); if (admin && admin.password === password) { onLoginSuccess(admin); setIsOpen(false); } else { setError(true); } }} className="space-y-4">
          <select className="w-full px-4 py-3 bg-slate-50 border rounded-xl outline-none" value={selectedId} onChange={(e) => setSelectedId(e.target.value)}>
            {admins.map(a => <option key={a.id} value={a.id}>{a.name}</option>)}
          </select>
          <input autoFocus type="password" placeholder="Sandi" className={`w-full px-4 py-3 bg-slate-50 border ${error ? 'border-red-500' : 'border-slate-200'} rounded-xl outline-none`} value={password} onChange={(e) => { setPassword(e.target.value); setError(false); }} />
          <div className="grid grid-cols-2 gap-3 pt-4">
            <button type="button" onClick={() => setIsOpen(false)} className="py-3 text-slate-500 font-bold hover:bg-slate-100 rounded-xl">Batal</button>
            <button type="submit" className="bg-blue-600 text-white py-3 rounded-xl font-bold">Masuk</button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default App;
